import KKWAE from "../../Tools/KKWAE";
import React from 'react';

import './menu.scss';
import {boundClass} from 'autobind-decorator';
import { TextTooltip, Tooltip } from "../Tooltip";
import { changePage } from "../../Tools/PageRender";
import { Dialog } from "../Dialog";


const menuItems = [
    {icon: 'fas fa-map', text: ''},
    {icon: 'fas fa-user', text: ''},
    {icon: 'fas fa-hat-wizard', text: '  '},
    {icon: 'fas fa-mountain', text: ''},
    {icon: 'fas fa-gamepad', text: ''}
];
@boundClass
export class Menu extends KKWAE<{}, Attrib.State.Menu> {
    state: Attrib.State.Menu = {
        active: false
    };
    onMenuClick(): void {
        this.setState({active:true})
        KKWAE.trigger('@tooltip-off')
    }
    goToAreasPage(): void {
        changePage('areas')
    }
    menuAction(type: string): void {
        switch(type){
            case '':
                changePage('index')
                break;
            case '':
                Dialog.show(<Dialog title=" " width={250} height={100}>   .</Dialog>)
                break;
            case '  ':
                Dialog.show(<Dialog title=" " width={250} height={100}>   .</Dialog>)
                break;
            case '':
                changePage('areas')
                break;
            case '':
                changePage('connellow')
                break;
        }
    }
    render(){
        return (
            <>
                {
                    !this.state.active ? 
                    <div className="menu-hover" onClick={this.onMenuClick} {...Tooltip.register(TextTooltip, "  ")}>
                        <i className="fas fa-caret-down"></i>
                    </div>
                    : null
                }
                <div 
                    className="menu" 
                    style={this.state.active ? {left:0} : {left:-250}} 
                    onMouseLeave={() => this.setState({active:false})}
                >
                    <div className="menu-head">
                        <div className="menu-head-title"> </div>
                        <div className="menu-head-subtitle"></div>
                    </div>
                    {
                        menuItems.map(item => <div className="menu-item" onClick={() => this.menuAction(item.text)}><i className={item.icon} />{item.text}</div>)
                    }
                </div>
            </>
        )
    }
}